function formatSecond(time: number) {
	const seconds = time % 60
	time = ~~(time / 60)
	const minutes = time % 60
	time = ~~(time / 60)
	const hours = time % 24
	time = ~~(time / 24)
	const days = time
	if (days) return `${days}  ${hours}  ${minutes}  ${seconds} `
	if (hours) return `${hours}  ${minutes}  ${seconds} `
	if (minutes) return `${minutes}  ${seconds} `
	if (seconds) return `${seconds} `
}

export { formatSecond }